
import VueSlider from 'vue-slider-component'
import 'vue-slider-component/theme/default.css'
import Vue, {
  ComponentPublicInstance,
  DefineComponent,
  defineComponent
} from 'vue'
export default defineComponent({
  name: 'SliderInput',
  props: {
    title: {
      type: String,
      default: ''
    },
    modelValue: {
      type: Number,
      required: true
    },
    min: {
      type: Number,
      default: -Math.max()
    },
    max: {
      type: Number,
      default: Math.max()
    },
    interval: {
      type: Number,
      default: 1
    },
    minMarker: {
      type: String,
      default: ''
    },
    maxMarker: {
      type: String,
      default: ''
    },
    label: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    }
  },
  components: { VueSlider },
  data() {
    return {
      focused: false,
      temp: null
    }
  },
  computed: {
    getSetFormattedValue: {
      get(vm: Vue.DefineComponent): any {
        const amount = vm.modelValue
        return vm.thousandSeprator(amount)
      },
      set(value: any) {
        const textAmount = value.toString() || ''
        const noCommasAmount = textAmount.replace(/,/g, '')
        const numberAmount = Number(noCommasAmount)
        this.$emit('update:modelValue', Math.min(this.max, numberAmount))
      }
    },
    getSetValue: {
      get(vm: Vue.DefineComponent) {
        return vm.modelValue
      },
      set(value: number) {
        this.$emit('update:modelValue', value)
      }
    }
  },
  methods: {
    onFocus() {
      this.focused = true
    },
    onBlur() {
      this.focused = false
      this.$emit('update:modelValue', Math.max(this.min, this.modelValue))
    },
    thousandSeprator(amount: any) {
      if (
        amount !== '' ||
        amount !== undefined ||
        amount !== 0 ||
        amount !== '0' ||
        amount !== null
      ) {
        return amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',')
      } else {
        return amount
      }
    }
  }
})
